/* global clearInterval, console, CustomFunctions, setInterval */

/**
 * Returns the Azure OpenAI response.
 * @customfunction
 * @param message String to write.
 * @returns String with the answer to the prompt.
 */
export async function AI_AZURE(message: string): Promise<string> {
  try {
    const apiUrl = Office.context.document.settings.get('azure.apiUrl');
    //const endpoint = Office.context.document.settings.get('azure.endpoint');
    const apiKey = Office.context.document.settings.get('azure.apiKey');
    const apiVersion = Office.context.document.settings.get('azure.apiVersion');
    const deployment = Office.context.document.settings.get('azure.deploymentName');
    const temperature = Office.context.document.settings.get('azure.temperature');
    const maxTokens = Office.context.document.settings.get('azure.maxTokens') || 1000;

    if (!apiKey) return "Error: API key is not set";
    if (!apiVersion) return "Error: API version is not set";
    if (!deployment) return "Error: Deployment name is not set";
    if (!apiUrl) return "Error: API URL is not set";

    const { default: OpenAI } = await import("openai");
    const client = new OpenAI({
      baseURL: apiUrl,
      apiKey: apiKey,
      defaultQuery: { 'api-version': apiVersion },
      dangerouslyAllowBrowser: true
    });

    const response = await client.chat.completions.create({
      model: deployment,
      messages: [{ role: "user", content: message }],
      temperature: parseFloat(temperature),
      max_tokens: parseInt(maxTokens)
    });

    return response.choices[0]?.message?.content || "Error: Empty response";
  } catch (error) {
    return `Error: Azure OpenAI - ${error.message}`;
  }
}

/**
 * Returns the OpenAI response.
 * @customfunction
 * @param message String to write.
 * @returns String with the answer to the prompt.
 */
export async function AI_OPENAI(message: string): Promise<string> {
  try {
    const apiKey = Office.context.document.settings.get('openAI.apiKey');
    const model = Office.context.document.settings.get('openAI.model') || 'gpt-4o-mini';
    const temperature = Office.context.document.settings.get('openAI.temperature') || 0.7;
    const maxTokens = Office.context.document.settings.get('openAI.maxTokens') || 1000;

    if (!apiKey) return "Error: API key is not set";
    if (!model) return "Error: Model is not set";

    const { default: OpenAI } = await import("openai");
    const client = new OpenAI({
      apiKey: apiKey,
      dangerouslyAllowBrowser: true
    });

    const response = await client.chat.completions.create({
      model: model,
      messages: [{ role: "user", content: message }],
      temperature: parseFloat(temperature),
      max_tokens: parseInt(maxTokens)
    });

    return response.choices[0]?.message?.content || "Error: Empty response";
  } catch (error) {
    return `Error: OpenAI - ${error.message}`;
  }
}

/**
 * Returns the Google Gemini AI response.
 * @customfunction
 * @param message String to write.
 * @returns String with the answer to the prompt.
 */
export async function AI_GEMINI(message: string): Promise<string> {
  try {
    const apiKey = Office.context.document.settings.get('gemini.apiKey');
    const model = Office.context.document.settings.get('gemini.model') || 'gemini-2.0-flash';
    const temperature = Office.context.document.settings.get('gemini.temperature') || 0.7;
    const maxTokens = Office.context.document.settings.get('gemini.maxTokens') || 1000;

    if (!apiKey) return "Error: API key is not set";
    if (!model) return "Error: Model is not set";

    const { GoogleGenAI } = await import("@google/genai");
    const ai = new GoogleGenAI({ apiKey: apiKey });

    const response = await ai.models.generateContent({
      model: model,
      contents: message,
      config: {
        temperature: parseFloat(temperature),
        maxOutputTokens: parseInt(maxTokens)
      }
    });

    return response.text || "Error: Empty response";
  } catch (error) {
    return `Error: Google Gemini - ${error.message}`;
  }
}

/**
 * Returns the Anthropic AI response.
 * @customfunction
 * @param message String to write.
 * @returns String with the answer to the prompt.
 */
export async function AI_CLAUDE(message: string): Promise<string> {
  try {
    const apiKey = Office.context.document.settings.get('claude.apiKey');
    const model = Office.context.document.settings.get('claude.model') || 'claude-3-opus-20240229';
    const temperature = Office.context.document.settings.get('claude.temperature') || 0.7;
    const maxTokens = Office.context.document.settings.get('claude.maxTokens') || 1000;

    if (!apiKey) return "Error: API key is not set";
    if (!model) return "Error: Model is not set";

    const { default: Anthropic } = await import("@anthropic-ai/sdk");
    const anthropic = new Anthropic({
      apiKey: apiKey,
      dangerouslyAllowBrowser: true
    });

    const response = await anthropic.messages.create({
      model: model,
      max_tokens: parseInt(maxTokens),
      temperature: parseFloat(temperature),
      messages: [
        {
          role: "user",
          content: [
            {
              type: "text",
              text: message
            }
          ]
        }
      ]
    });

    return response.content[0].text || "Error: Empty response";
  } catch (error) {
    return `Error: Claude - ${error.message}`;
  }
}

CustomFunctions.associate("AI_AZURE", AI_AZURE);
CustomFunctions.associate("AI_OPENAI", AI_OPENAI);
CustomFunctions.associate("AI_GEMINI", AI_GEMINI);
CustomFunctions.associate("AI_CLAUDE", AI_CLAUDE);